export default {
  name: "search.bar",
  props: {
    show: undefined
  }
  ,
  data() {
    return {
      keyword: '',
    };
  },
  mounted() {
    document.addEventListener('mouseup', this.on_mouseup);
  },
  destroyed() {
    document.removeEventListener("mouseup", this.on_mouseup);
  },
  methods: {
    on_mouseup: function (e) {
      let tree = this.$refs.container
      if (tree) {
        if (!tree.contains(e.target)) {
          this.show = false
        }
      }
    },
    search_submit: function () {
      var router_url = '/search/' + this.keyword;
      console.log('router_url:', router_url);
      this.$router.push(router_url);
      return false;
    }
  }
}
